  <view class="container" :class="{ 'care-mode': careMode }">
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <view class="header" :style="{ paddingTop: statusBarHeight + 'px' }">
      <view class="nav-left" @click="goBack">
        <text class="back-icon">‚Üê</text>
      </view>
