    <!-- ËÆæÁΩÆÂÜÖÂÆπ -->
    <view class="content">
      <view class="settings-card">
        <view class="setting-item" @click="goToProfile">
          <text class="setting-icon">üë§</text>
          <text class="setting-text">‰∏™‰∫∫ËµÑÊñô</text>
          <text class="setting-arrow">></text>
        </view>
        <view class="setting-divider"></view>
        <view class="setting-item" @click="toggleCareMode">
          <text class="setting-icon">üîç</text>
