<script>
import careModeMixin from '@/mixins/careMode.js'

export default {
  mixins: [careModeMixin],
  
  data() {
    return {
      statusBarHeight: 0
    }
  },
  
  onLoad() {
    this.getStatusBarHeight();
    this.loadCareMode();
  },
  
  onShow() {
    this.loadCareMode();
  },
  
  methods: {
    // Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
    getStatusBarHeight() {
      const systemInfo = uni.getSystemInfoSync();
      this.statusBarHeight = systemInfo.statusBarHeight || 0;
    },
    
    // ËøîÂõû‰∏ä‰∏ÄÈ°µ
    goBack() {
      uni.navigateBack({
        delta: 1
      });
    },
    
    // Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫ËµÑÊñôÁºñËæëÈ°µÈù¢
    goToProfile() {
      uni.navigateTo({
        url: '/pages/profile/edit-profile'
      });
    },
    
    // Â§ÑÁêÜÈÄÄÂá∫ÁôªÂΩïÈÄªËæë
    handleLogout() {
      // 1. ÊòæÁ§∫Á°ÆËÆ§ÂºπÁ™óÔºàÈÅøÂÖçËØØÊìç‰ΩúÔºâ
        uni.showModal({
          title: 'Á°ÆËÆ§ÈÄÄÂá∫',
          content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÂΩìÂâçË¥¶Âè∑ÂêóÔºü',
          confirmText: 'ÈÄÄÂá∫',
          cancelText: 'ÂèñÊ∂à',
          confirmColor: '#ff4d4f', // Âº∫Ë∞ÉËâ≤ÔºàÁ∫¢Ëâ≤Ôºâ
          success: (res) => {
            if (res.confirm) {
              // 2. Ê∏ÖÈô§ÁôªÂΩïÁä∂ÊÄÅ
              uni.removeStorageSync('wechatLoginToken'); // Ê∏ÖÈô§ÁôªÂΩïtoken
              uni.removeStorageSync('userInfo'); // Ê∏ÖÈô§Áî®Êà∑‰ø°ÊÅØ
              uni.removeStorageSync('isLoggedIn'); // Ê∏ÖÈô§ÁôªÂΩïÁä∂ÊÄÅÊ†áËÆ∞
              
              // 3. Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫‰∏≠ÂøÉÈ°µÈù¢ÔºàËÄåÈùûÁôªÂΩïÈ°µÔºâ
              uni.redirectTo({
                url: '/pages/index/index?tab=4', // ÂÅáËÆæ‰∏™‰∫∫‰∏≠ÂøÉÊòØÁ¨¨4‰∏™tab
                success: () => {
                  // 4. ÊèêÁ§∫ÈÄÄÂá∫ÊàêÂäü
                  uni.showToast({
                    title: 'ÈÄÄÂá∫ÊàêÂäü',
                    icon: 'success',
                    duration: 1500
                  });
                },
                fail: (err) => {
                  console.error('Ë∑≥ËΩ¨‰∏™‰∫∫‰∏≠ÂøÉÂ§±Ë¥•Ôºö', err);
                  uni.showToast({
                    title: 'ÈÄÄÂá∫Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
                    icon: 'none'
                  });
                }
              });
            }
          }
        });
    }
  }
}
